.container {
    width:100%;
    height:100%;
    background-size: cover;
    position: fixed;
    align-items: center;
    text-align: center;
    background-color: #00546b;
    justify-content: center;
    align-items: center;  
}

/* Log in */
.title {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: aliceblue;
  display: flex;
  width: max-content;
  justify-self: center;
  position: absolute;
}

.Links {
  position: absolute;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  bottom: 0;
  width: 80%;
  height: 9.5%;
}

.LogInPane {
  position: absolute;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  display: flex;
  justify-content: center; 
  flex-direction: column;
  align-items: center;  
  width:100%;
  height:100%;
  color: aliceblue;
  font-size:8em;
  font-weight: bolder;
}

.pt {
  padding-top: 2%;
}

/* modal stuff */
.modal {
  position: fixed;
  max-width: 60%;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color:azure;
  display: flex;
  align-items: center;
  justify-content: center;
  display: flex;
  padding: 5%;
  margin: 10%;
}

.TitleContent {
  font-size: 2em;
  color: #282c34;
  padding: 1%;
  display: inline-block;
}

.AboutContent {
  color: #616264;
}

.CloseModalBtn {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #5adaff 0, #5468ff 100%);
  border: 0;
}

.FancyBlueButton {
  align-items: center;
  appearance: none;
  background-image: radial-gradient(100% 100% at 100% 0, #646464 0, #2c2c2e 100%);
  border: 0;
  border-radius: 4px;
  box-shadow: rgba(45, 35, 66, .4) 0 2px 4px,rgba(45, 35, 66, .3) 0 7px 13px -3px,rgba(58, 65, 111, .5) 0 -3px 0 inset;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: bolder;
  justify-content: center;
  line-height: 1;
  list-style: none;
  margin:1%; 
  overflow: hidden;
  padding: 1%;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: box-shadow .15s,transform .15s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  will-change: box-shadow,transform;
  font-size: 18px;
  width: 200px;
  max-height: 60%;
}

.FancyBlueButton:focus {
  box-shadow: #000000 0 0 0 1.5px inset, rgba(45, 35, 66, .4) 0 2px 4px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #3c4fe0 0 -3px 0 inset;
}

.FancyBlueButton:hover {
  box-shadow: rgba(45, 35, 66, .4) 0 4px 8px, rgba(45, 35, 66, .3) 0 7px 13px -3px, #262629 0 -3px 0 inset;
  transform: translateY(-2px);
}

.FancyBlueButton {
  box-shadow: #000000 0 3px 7px inset;
  transform: translateY(2px);
}

.problemImg {
  display: block;
  max-width: 50%;
  height: auto;
  margin:auto;
}

.problemContainer {
  display:flexbox;
}

.grid {
  display: grid;
}


.teamModal {
  align-items: center;
  text-align: center;
  max-height: 50%;
  padding: 1%;
  margin: 1%;
  position: relative;
  display: grid;
}

.Card-Container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
  background-color: #282c34;
}

Card {
  display: flex;
  margin:auto;
  background-color: #616264;
  border: #3c4fe0;
}

.dash {
  height: 100%;
  width: 100%;
}

.bgVid {
  object-fit: cover;
  height: 100vh;
  width: 100vw;
  z-index: 3;
}

@media screen and (max-width: 600px) {
  .Card-Container {
    grid-template-columns: repeat(1, 1fr);
  }

  .Links {
    flex-direction: column;
    height: 20%;
  }

  .title {
    font-size: 2em;
  }

  .LogInPane {
    font-size: 2em;
    margin-bottom: 2%;
  }
}
  
